/* Global styles */
body {
  background-color: #f3f4f6;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Container */
.container {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 480px;
  padding: 20px;
  text-align: center;
}

/* Heading */
h1 {
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Input field */
input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 16px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

input[type="text"]:focus {
  outline: none;
  border-color: #4F8BF9;
}

input[type="text"]:focus-visible {
  border-color: #4F8BF9;
}

/* Button */
button {
  width: 100%;
  padding: 12px;
  border-radius: 5px;
  background-color: #4F8BF9;
  color: white;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #005bb5;
}

button:disabled {
  background-color: #d1d5db;
  cursor: not-allowed;
}

/* Thumbnail */
img {
  max-width: 100%;
  border-radius: 8px;
}

/* Error message */
.mt-4 {
  margin-top: 16px;
  font-size: 1rem;
  color: #e74c3c;
}

.mt-6 {
  margin-top: 24px;
}

/* Utility classes */
.space-y-4 > *:not(:last-child) {
  margin-bottom: 16px;
}

.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.p-2 {
  padding: 8px;
}

.border {
  border: 1px solid #e2e8f0;
}

.rounded {
  border-radius: 6px;
}

.bg-green-500 {
  background-color: #38a169;
}

.text-white {
  color: white;
}

.bg-green-600:hover {
  background-color: #2f855a;
}

.transition {
  transition: all 0.3s ease;
}

.text-gray-700 {
  color: #4a5568;
}

.text-lg {
  font-size: 1.125rem;
}

.text-center {
  text-align: center;
}

/* Responsive design */
@media (max-width: 480px) {
  .container {
    padding: 16px;
  }
  h1 {
    font-size: 1.25rem;
  }
}
